var that;
var videoCtx;
export default {
	data() {
		return {
			swiperList: [],
			dotStyle: false,
			towerStart: 0,
			direction: '',
			pjlist: [],
			gid: '',
			goods: '',
			attr: '',
			detail: '',
			modal: false,
			modali: false,
			allmodal: false,
			prinattr: [],
			num: 1,
			attrgoods: '',
			buytype: 1,
			userinfo: uni.getStorageSync('userinfo'),
			show: false,
			fx: '',
			play: false,
			kefupara: '',
			jsonUser: '',
			tabList: ['商品详情', '商品评价'],
			TabCur: 0,
			comment: [],
			nolsit: false,
			nomore: false,
			page: 1
		};
	},
	methods: {
		tabSelect(e) {
			that.TabCur = e.currentTarget.dataset.id;
		},
		showModel(e) {
			that.allmodal = true;
		},
		tohome() {






			uni.switchTab({
				url: '/pages/index/index'
			});

		},
		tocard() {
			uni.navigateTo({
				url: '/pages/cart/cart'
			});
		},
		tpmchinfo(e) {
			uni.navigateTo({
				url: '/pages/mch/mchinfo/mchinfo?mch_id=' + e.currentTarget.dataset.id
			});
		},
		getinfo() {
			this.$request.request({
				url: that.api.default.goods,
				data: {
					id: that.gid
				},
				success(res) {
					that.goods = res;
					that.goods['cover_pic'] = res.pic_list[0].pic_url;
					var attr = res.attr_group_list;
					that.attr = attr;
					var detail = res.detail.replace(/\<img/g, '<img style="width:100%;height:auto;display:block" ');
					that.detail = detail;
				}
			});
		},
		hideModal() {
			that.modal = !that.modal;
		},
		tmodal() {
			that.modali = !that.modali;
		},
		pint(e) {
			that.kd = e.currentTarget.dataset.id;
		},
		getall(e) {
			if (e) {
				//判断但规格直接购买
				if (e.currentTarget.dataset.ok) {
					if (that.prinattr.length == that.attr.length) {
						if (e.currentTarget.dataset.type != 1) {
							if (that.paytype == 2) {
								console.log('attrgoods', that.attrgoods);
								if (that.attrgoods) {
									if (that.attrgoods.num == 0) {
										that.$alert('当前商品已售罄!');
										that.allmodal = !that.allmodal;
										return;
									}
								} else {
									console.log('goods', that.goods.num);
									if (that.goods.num == 0) {
										that.$alert('当前商品已售罄!');
										that.allmodal = !that.allmodal;
										return;
									}
								}
								that.buy();
							}
							if (that.paytype == 3) {
								that.addcart();
							}
							that.allmodal = !that.allmodal;
						}
					} else {
						that.$alert('请选择规格');
						return;
					}
				} else {
					if (that.attr.length == 1 && that.attr[0].attr_list.length == 1) {
						console.log('单规格');
						that.attr[0].attr_list[0]['type'] = true;
						var v = {
							attr_group_id: that.attr[0].attr_group_id,
							attr_id: that.attr[0].attr_list[0].attr_id,
							attr_name: that.attr[0].attr_list[0].attr_name
						};
						that.prinattr[0] = v;
						that.$forceUpdate();
						console.log('prinattr：', that.prinattr);
					}
					that.allmodal = !that.allmodal;
					that.paytype = e.currentTarget.dataset.type;
				}
			} else {
				that.allmodal = !that.allmodal;
			}
		},
		//评论列表
		getcomlist() {
			let page = that.page;
			let comment = that.comment;
			let nolist = that.nolist;
			let nomore = that.nomore;
			this.$request.request({
				url: that.api.default.comment_list,
				data: {
					goods_id: that.gid,
					page: page
				},
				success(res) {
					let list = res.list;
					if (page == 1) {
						comment = list;
						page = 2;
						comment.length == 0 ? (nolist = true) : (nolist = false);
						list.length == 0 ? ((that.lodstatus = ''), (that.nolist = true)) : list.length > 10 ? (that.lodstatus = 'more') : (that.lodstatus = '');
					} else {
						if (list.length > 0) {
							comment = comment.concat(list);
							page++;
							that.lodstatus = 'more';
						} else {
							that.lodstatus = 'noMore';
							that.nolist = true;
						}
					}
					that.comment = comment;
					that.page = page;
					that.nolist = nolist;
					that.nomore = nomore;
				}
			});
		},
		tplist() {
			uni.navigateTo({
				url: '/pages/evaluateList/evaluateList?id=' + that.gid
			});
		},
		// 打开客服
		kefu() {},

		// 加入购物车
		addcart() {
			if (!uni.getStorageSync('access_token')) {
				that.$zax.yzlogin();
				return;
			}
			if (that.prinattr.length != that.attr.length) {
				that.getall();
				return;
			}
			console.log('商品id:', that.gid);
			console.log('规格信息：', that.prinattr);
			console.log('数量：', that.num);
			// return;
			this.$request.request({
				url: that.api.cart.add_cart,
				data: {
					goods_id: that.gid,
					attr: JSON.stringify(that.prinattr),
					num: that.num
				},
				method: 'post',
				success(res) {
					this.$alertok('添加购物车成功');
				}
			});
		},
		// 选择规格
		getattr(e) {
			var attr_group_id = e.currentTarget.dataset.gid;
			var attr_id = e.currentTarget.dataset.id;
			var attr_name = e.currentTarget.dataset.name;
			for (let t = 0; t < that.attr.length; t++) {
				if (that.attr[t].attr_group_id == attr_group_id) {
					console.log('当前大类:', that.attr[t]);
					for (let a = 0; a < that.attr[t].attr_list.length; a++) {
						if (that.attr[t].attr_list[a].attr_id == attr_id) {
							console.log('选择的小类：', that.attr[t].attr_list[a]);
							that.attr[t].attr_list[a]['type'] = true;
							that.$forceUpdate();
						} else {
							that.attr[t].attr_list[a]['type'] = false;
							that.$forceUpdate();
						}
					}
				}
			}
			// 添加规格
			var list = [];
			for (let t = 0; t < that.attr.length; t++) {
				for (let a = 0; a < that.attr[t].attr_list.length; a++) {
					if (that.attr[t].attr_list[a]['type'] == true) {
						var v = {
							attr_group_id: that.attr[t].attr_group_id,
							attr_id: that.attr[t].attr_list[a].attr_id,
							attr_name: that.attr[t].attr_list[a].attr_name
						};
						// console.log("点击的", v);
						list = list.concat(v);
						that.$forceUpdate();
					}
				}
			}
			that.prinattr = list;
			// console.log('选择规格:', that.prinattr);
			// console.log("规格列表：", that.attr)
			that.getattringo();
			that.$forceUpdate();
		},

		comput(e) {
			var type = e.currentTarget.dataset.type;
			if (type == 0) {
				that.num = that.num + 1;
			} else {
				if (that.num == 1) {
					return;
				}
				that.num = that.num - 1;
			}
		},
		buy() {
			if (!uni.getStorageSync('access_token')) {
				that.$zax.yzlogin();
				return;
			}
			if (that.prinattr.length != that.attr.length) {
				that.getall();
				return;
			}
			var mch_list = [
				{
					mch_id: that.goods.mch ? that.goods.mch.id : '0',
					goods_list: [
						{
							goods_id: that.gid,
							attr: that.prinattr,
							num: that.num
						}
					]
				}
			];
			console.log('订单信息：', mch_list);
			this.$request.request({
				url: that.api.order.new_submit_preview,
				data: {
					mch_list: JSON.stringify(mch_list)
				},
				method: 'post',
				success(res) {
					console.log(res);
					uni.setStorageSync('order', res);
					uni.redirectTo({
						url: '/pages/order/createOrder/createOrder'
					});
				}
			});
		},
		//获取规格信息
		getattringo() {
			var attr_list = [];
			for (let i in that.prinattr) {
				attr_list.push(that.prinattr[i].attr_id);
			}
			that.$request.request({
				url: that.api.default.goods_attr_info,
				data: {
					goods_id: that.gid,
					attr_list: JSON.stringify(attr_list),
					group_checked: 0,
					group_id: 0
				},
				success(res) {
					if (res.code == 1) {
						this.$alert(res.msg);
					} else {
						console.log('规格信息', res);
						that.attrgoods = res;
					}
				}
			});
		},
		previewImage(e) {
			console.log(e.currentTarget.dataset.list);
			uni.previewImage({
				urls: e.currentTarget.dataset.list,
				current: e.currentTarget.dataset.img
			});
		},
		playvideo() {
			that.play = true;
			videoCtx = uni.createVideoContext('myVideo');
			setTimeout(function() {
				videoCtx.requestFullScreen();
				0;
				videoCtx.play();
			}, 100);
		},
		fullscreenchange(e) {
			if (!e.detail.fullScreen) {
				videoCtx.stop();
				setTimeout(function() {
					that.play = false;
				}, 1000);
			}
		},
		// favorite-add 收藏
		favoriteAddRem(e) {
			let favorite = e.currentTarget.dataset.favorite;
			console.log(favorite);
			let url = favorite != 0 ? that.api.user.favorite_remove : that.api.user.favorite_add;
			that.$request.request({
				url: url,
				data: {
					goods_id: that.gid
				},
				method: 'post',
				success(res) {
					that.getinfo();
				}
			});
		}
	},
	onLoad(option) {
		that = this;
		that.gid = option.gid ? option.gid : '6';
		that.getinfo();
		that.getcomlist();
		that.userinfo = uni.getStorageSync('userinfo');
		videoCtx = uni.createVideoContext('myVideo');
		that.jsonUser = uni.getStorageSync('jsonUser');
	},
	onBackPress() {
		if (!that.allmodal) {
			that.allmodal = !that.allmodal;
			return;
		}
	},
	onShareAppMessage() {
		var goods = that.goods;
		return {
			imageUrl: goods.cover_pic,
			title: goods.name,
			path: '/pages/goods/goodsinfo/goodsinfo?gid=' + goods.id + '&user_id=' + that.userinfo.id,
			success: function(res) {
				console.log('成功', res);
			}
		};
	}
};
